<template>
  <div id="app">
    <!-- <keep-alive>
      <router-view/>
    </keep-alive> -->
    <router-view/>
  </div>
</template>
<script>
import {getWxConfig} from './utils/apis.js'
import wx from 'weixin-js-sdk'
export default {
  name: 'App',
  beforeCreate() {
    let ua = navigator.userAgent.toLowerCase(); 
    if(ua.match(/MicroMessenger/i)=="micromessenger") {  //微信内
        console.log('微信内')
    } else {  
      console.log('浏览器')
      // window.location.href = window.location.origin+'/h5_crm/User/login?front=home';
    }

    let params = {
      url: encodeURIComponent(window.location.href.split('#')[0])
      // url:'https://www.enjoygo.top'
    };
    getWxConfig(params).then(res => {
      console.log(res);
      let Data = res.data;
      // config信息验证后会执行ready方法，所有接口调用都必须在config接口获得结果之后，config是一个客户端的异步操作
      wx.config({
        debug: false, // 开启调试模式,开发时可以开启
        appId: Data.appId, // 必填，公众号的唯一标识   由接口返回
        timestamp: Data.timestamp, // 必填，生成签名的时间戳 由接口返回
        nonceStr: Data.nonceStr, // 必填，生成签名的随机串 由接口返回
        signature: Data.signature, // 必填，签名 由接口返回
        jsApiList: [
          'onMenuShareTimeline', // 分享到朋友圈
          'onMenuShareAppMessage', // 分享到微信好友
          // 'onMenuShareQQ', // 分享到QQ
                // 'onMenuShareWeibo', // 分享到微博
                // 'onMenuShareQZone',// 分享到空间
                //"hideMenuItems", // 批量隐藏菜单
        ] //此处填你所用到的方法
      });
    })
    let category_id = null
    let cururl = window.location.href
    if(cururl.indexOf('categoryId')>-1){
      category_id = cururl.substr(cururl.indexOf('categoryId')).split("=")[1]
      console.log("我是"+category_id)
    }else{
      category_id = 0
    }
    window.localStorage.setItem("categoryId", category_id);
  }
}

</script>

