<script>
import {articleDetail} from '../../utils/apis.js'
import wx from 'weixin-js-sdk'
export default {
  data() {
    return {
      detailinfo: {},
      isclickshare:false,
      isClickcustomer:false,
      serviceqrcode:'https://cdn.jsdelivr.net/gh/objstore/static001/images/wx_qr_code.jpg'
    };
  },
  methods: {
	GetDetail() {
		let self = this
		let params = {
			id:self.pid
		};
		articleDetail(params).then(res => {
			self.detailinfo = res.data
			self.ptitle = res.data.title
			self.ShareArticle()
		});
	},
    GoList(){
		window.scrollTo(0,0);
		this.$router.push({
			path: "/list/article",
			query: {
				categoryId:0
			}
		});
    },
    IsOpenShare(){
		this.isclickshare = !this.isclickshare
    },
    IsOpenCustomer() {
      this.isClickcustomer = !this.isClickcustomer;
    },
    //分享
    ShareArticle() {
		let self = this
      // let reurl = window.location.href;
      // let slink = reurl + '/detail/article?pid=' + self.pid;
      wx.ready(function () {
        wx.onMenuShareAppMessage({ // 分享给朋友
          title: self.ptitle, // 分享标题
          desc: self.detailinfo.description,
          link: window.location.href, // 分享链接
          imgUrl: self.detailinfo.pic, // 分享图标
          success: function () {
          },
          cancel: function () { }
        });
        wx.onMenuShareTimeline({ //分享到朋友圈
          title: self.ptitle, // 分享标题
          link: window.location.href,// 分享链接
          imgUrl: self.detailinfo.pic, // 分享图标
          success: function () {
          },
          cancel: function () { }
        });
      });
    },

  },
  created() {
    this.pid = this.$route.query.pid?this.$route.query.pid:6020
    this.ptitle = this.$route.query.ptitle?this.$route.query.ptitle:"ENJOYGO享购"
    this.GetDetail()
  }
};
</script>
